/* eslint-disable no-cond-assign */
export const objectType = (obj) =>
  Object.prototype.toString.call(obj).slice(8, -1);
export const isDefined = (param) => typeof param !== 'undefined';
export const isUndefined = (param) => typeof param === 'undefined';
export const isFunction = (param) => typeof param === 'function';
export const isNumber = param => typeof param === 'number' && !isNaN(param); // eslint-disable-line
export const isString = (str) => objectType(str) === 'String';
export const isArray = (arr) => objectType(arr) === 'Array';

export const closest = (target, selector) => {
  // closest(e.target, '.field')
  while (target) {
    // eslint-disable-line
    if (target.matches && target.matches(selector)) return target;
    if (target.msMatchesSelector && target.msMatchesSelector(selector))
      return target;
    // eslint-disable-next-line no-param-reassign
    target = target.parentNode;
  }
  return null;
};

export const getOffsetRect = (elem) => {
  // (1)
  const box = elem.getBoundingClientRect();

  const { body, documentElement } = document;

  // (2)
  const scrollTop =
    window.pageYOffset || documentElement.scrollTop || body.scrollTop;
  const scrollLeft =
    window.pageXOffset || documentElement.scrollLeft || body.scrollLeft;

  // (3)
  const clientTop = documentElement.clientTop || body.clientTop || 0;
  const clientLeft = documentElement.clientLeft || body.clientLeft || 0;

  // (4)
  const top = box.top + scrollTop - clientTop;
  const left = box.left + scrollLeft - clientLeft;

  return { top: Math.round(top), left: Math.round(left) };
};

export const getTotalScroll = (elem) => {
  let top = 0;
  let left = 0;

  // eslint-disable-next-line no-param-reassign
  while ((elem = elem.parentNode)) {
    top += elem.scrollTop || 0;
    left += elem.scrollLeft || 0;
  }

  return { top, left };
};

export const getTransformProps = (x, y) => ({
  transform: `translate(${x}px, ${y}px)`,
});

export const listWithChildren = (list, childrenProp) =>
  list.map((item) => ({
    ...item,
    [childrenProp]: item[childrenProp]
      ? listWithChildren(item[childrenProp], childrenProp)
      : [],
  }));

export const getAllNonEmptyNodesIds = (items, childrenProp) => {
  let childrenIds = [];
  const ids = items
    .filter((item) => item[childrenProp].length)
    .map((item) => {
      childrenIds = childrenIds.concat(
        getAllNonEmptyNodesIds(item[childrenProp], childrenProp),
      );
      return item.id;
    });

  return ids.concat(childrenIds);
};

export const isInternetExplorer = () => {
  const ua = window.navigator.userAgent;
  const msie = ua.indexOf('MSIE ');
  // If Internet Explorer, return version number
  if (msie > 0 || !!navigator.userAgent.match(/Trident.*rv:11\./)) {
    return true;
  }
  return false;
};
